.header__burger {
  position: absolute;
  width: 18px;
  height: 12px;
  top: 50%;
  margin-top: -6px;
  right: 0;
}

.header__burger span {
  display: block;
  height: 1px;
  position: absolute;
  width: 100%;
  top: 5px;
  left: 0;
  background-color: #fff;
  transition: transform 0.2s ease-out, opacity 0.2s ease-out, background-color 0.2s ease-out;
}

.header__burger span:first-child {
  transform-origin: 0% 50%;
  top: 0;
}

.header__burger span:last-child {
  top: auto;
  bottom: 0;
  transform-origin: 0% 50%;
  transform: scaleX(0.6);
}

.header__button.active .header__burger span {
  transform: scaleX(0);
  opacity: 0;
  background-color: #00B9D2;
}

.header__button.active .header__burger span:first-child {
  transform: rotate(45deg) translate(1px, -2px);
  opacity: 1;
}

.header__button.active .header__burger span:last-child {
  transform: rotate(-45deg) scaleX(1) translate(1px, 2px);
  opacity: 1;
}

