.header {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: auto;
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__button {
    flex: 0 1 auto;
    position: relative;
    padding-right: 25px;
    margin-right: 15px;
  }
  &__burger {
    position: absolute;
    width: 18px;
    height: 12px;
    top: 50%;
    margin-top: -6px;
    right: 0;
    span {
      display: block;
      height: 1px;
      position: absolute;
      width: 100%;
      top: 5px;
      left: 0;
      background-color: $color-text;
      transition: transform $transition, opacity $transition, background-color $transition;
    }
    span:first-child {
      transform-origin: 0% 50%;
      top: 0;
    }
    span:last-child {
      top: auto;
      bottom: 0;
      transform-origin: 0% 50%;
      transform: scaleX(0.6);
    }    
  }

  &__button.active &__burger {
    span {
      transform: scaleX(0);
      opacity: 0;
      background-color: $color-accent;
    }
    span:first-child {
      transform: rotate(45deg) translate(1px, -2px);
      opacity: 1;
    }
    span:last-child {
      transform: rotate(-45deg) scaleX(1) translate(1px, 2px);
      opacity: 1;
    }
  }

  &__call {
    flex: 0 0 auto;
  }
}